import axios from 'axios';
// Ð‘Ð°Ð·Ð¾Ð²Ð° Ð°Ð´Ñ€ÐµÑÐ° API Pixabay
const BASE_URL = 'https://pixabay.com/api/';
// API-ÐºÐ»ÑŽÑ‡ Ð´Ð»Ñ Ð´Ð¾ÑÑ‚ÑƒÐ¿Ñƒ Ð´Ð¾ Pixabay API
const API_KEY = '50348782-72c1d2cf6f33e6e33f09bb691';
// Ð‘Ð°Ð·Ð¾Ð²Ð° Ð°Ð´Ñ€ÐµÑÐ° Ð´Ð»Ñ Ð²ÑÑ–Ñ… axios-Ð·Ð°Ð¿Ð¸Ñ‚Ñ–Ð²
axios.defaults.baseURL = BASE_URL;
// Ð—Ð°Ð³Ð¾Ð»Ð¾Ð²ÐºÐ¸ Ð´Ð»Ñ Ð·Ð°Ð¿Ð¸Ñ‚Ñ–Ð²
axios.defaults.params = {
  key: API_KEY,
  image_type: 'photo',
  orientation: 'horizontal',
  safesearch: true,
};

export async function getImagesByQuery(query) {
  const response = await axios.get('', { params: { q: query } });
  return response.data.hits;
}
